
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
body{line-height:1.6;font-family:-apple-system-font,"Helvetica Neue",sans-serif
}
*{margin:0;padding:0
}
a img{border:0
}
a{text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0)
}
::-webkit-input-placeholder{font-family:-apple-system-font,"Helvetica Neue",sans-serif
}
html,body{width:100%;height:100%;overflow:hidden;margin:0
}
#app{height:100%;position:relative
}
.hidden{display:none !important
}
.container{background-color:#f4f4f4
}
.icon-spirte{background-image:url(assets/images/icon-spirte.png);background-repeat:no-repeat;display:block;width:80px;height:80px
}
.vux-header{background-color:#a3bb6f !important
}
.weui_cell{padding:6px 10px
}
.weui_cell:before{left:0
}
.vux-no-group-title{font-size:16px;margin:0;-ms-flex:1;flex:1;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;width:100%
}
.vux-no-group-title .weui-cell__bd{width:100%;-ms-flex:1;flex:1;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column
}
.vux-no-group-title .weui-cell__bd .weui-textarea{width:100%;height:100%;-ms-flex:1;flex:1;overflow-y:auto
}
.slide-view{position:absolute;width:100%;transition:all .2s linear
}
.slide-view.slide-left-enter-active{transition:all .2s ease-in
}
.slide-left-enter,.slide-right-leave-active{transform:translate(100%, 0)
}
.slide-left-leave-active,.slide-right-enter{transform:translate(-20%, 0)
}
.slide-left-leave-active{opacity:.4
}
.main-view{width:100%;height:auto;position:absolute;left:0;right:0;top:46px;bottom:0
}
.main-view.has-tab{bottom:50px
}
.tab-bar{height:50px
}
.tab-bar .weui-tabbar__item{padding-top:5px
}
.tab-bar .weui-tabbar__item .tab-icon{width:100% !important;height:100% !important;background-size:91.125px 57.375px
}
.tab-bar .weui-tabbar__item .tab-icon.icon-user{background-position:0 0
}
.tab-bar .weui-tabbar__item .tab-icon.icon-tennis{background-position:-30.75px 0
}
.tab-bar .weui-tabbar__item .tab-icon.icon-list{background-position:-61.5px 0
}
.tab-bar .weui-tabbar__item span{margin-top:3px;color:#999
}
.tab-bar .weui-tabbar__item.weui-bar__item_on span{color:#a3bb6f
}
.tab-bar .weui-tabbar__item.weui-bar__item_on .icon-user{background-position:0 -30px
}
.tab-bar .weui-tabbar__item.weui-bar__item_on .icon-tennis{background-position:-30.75px -30px
}
.tab-bar .weui-tabbar__item.weui-bar__item_on .icon-list{background-position:-61.5px -30px
}


